body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.6;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
  margin: 0;
  padding: 0; }

h1, p, h2 {
  margin: 0;
  padding: 0; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

a:active,
a:visited,
a:focus {
  text-decoration: none; }

.wrapper {
  padding: 15px; }

.forms-item {
  margin-bottom: 20px; }

.forms-item__label {
  display: block; }

.forms-item__input {
  height: 20px;
  padding-left: 10px;
  font-family: "Roboto", sans-serif; }

.goal-items {
  display: flex; }
  .goal-items li {
    width: 60px;
    height: 60px;
    margin-right: 15px;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    text-align: center; }
    .goal-items li:last-cgild {
      margin-right: 0; }
    .goal-items li p:first-of-type {
      padding-top: 13px;
      font-size: 16px;
      line-height: 16px;
      color: #fff; }
    .goal-items li p:last-of-type {
      font-size: 12px;
      line-height: 16px;
      color: #fff; }

.goal-item {
  text-align: center;
  position: relative;
  user-select: none;
  width: 60px;
  height: 60px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  margin-right: 15px; }
  .goal-item:last-cgild {
    margin-right: 0; }
  .goal-item.active {
    background-color: #64e764; }
  .goal-item label {
    cursor: pointer;
    font-size: 16px;
    line-height: 16px;
    color: #fff;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    padding-top: 12px; }
    .goal-item label span {
      display: block;
      font-size: 12px;
      line-height: 16px;
      color: #fff; }
    .goal-item label input {
      width: 0;
      height: 0;
      position: absolute; }

.meet-block {
  margin-top: 40px; }

.js-yourname {
  font-weight: 700; }

.toggle-mnu {
  display: none;
  width: 28px;
  height: 28px;
  margin-top: 5px;
  position: absolute;
  top: 0;
  left: 30px;
  z-index: 100; }
  @media only screen and (max-width: 768px) {
    .toggle-mnu {
      display: block; } }
  .toggle-mnu span {
    position: relative;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #000;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
    .toggle-mnu span:after, .toggle-mnu span:before {
      content: "";
      position: absolute;
      left: 0;
      top: 9px; }
    .toggle-mnu span:after {
      top: 18px;
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
    .toggle-mnu span:before {
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
  .toggle-mnu.on span {
    background-color: transparent; }
    .toggle-mnu.on span:before {
      transform: rotate(45deg) translate(-1px, 0px); }
    .toggle-mnu.on span:after {
      transform: rotate(-45deg) translate(6px, -7px); }
